          <img :src="item.imgSrc" alt=""/>
        </template>
        <template slot="title">
          <div class="line_title">{{item.title}}</div>
          <!--          <van-tag plain round>{{item.remark}}</van-tag>-->
        </template>
      </van-cell>
    </van-cell-group>

    <template>
