      <!--        <template slot="default">-->
      <!--          <van-button plain type="info" icon="plus" size="mini" @click="recentClick(item)">常用</van-button>-->
      <!--        </template>-->
      <!--      </van-cell>-->
      <!--    </van-cell-group>-->

      <!--    <div class="sub_nav" @click="$router.push('/space/homework')">-->
      <!--      <span>推荐</span>-->
      <!--      <div type="text" style="font-weight: normal;">换一批</div>-->
      <!--    </div>-->
      <!--    <van-cell-group>-->
      <!--      <van-cell is-link class="cell_main" v-for="(item, index) in suggestList" :key="index" @click="suggestClick(item)">-->
      <!--        <template slot="icon">-->
      <!--          <img :src="item.imgSrc" alt=""/>-->
      <!--        </template>-->
      <!--        <template slot="title">-->
