      <!--          <span class="line_title">{{item.remark}}</span>-->
      <!--        </template>-->
      <!--      </van-cell>-->
      <!--    </van-cell-group>-->
    </template>

    <!-- 输入投屏码加入班级 -->
    <van-popup v-model="invitedShow">
      <r-digit-input ref="digit" title="请输入投屏码" sub-title="请在下方输入投屏码连接教室" :code-length="4" btn-name="连接教室"
                     code-name="投屏码" @submit="submitInvitedCode">
      </r-digit-input>
    </van-popup>
  </div>
</template>

<script>
import RDropdown from "@/components/r-dropdown";
import RDigitInput from "@/components/r-digit-input";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { formatDate } from "@/utils/formatData";
import defaultImg from "./../../assets/boy_img.png";

export default {
  name: "",
  components: { RDropdown, RDigitInput },
  props: {},
  data () {
    return {
      navList: [
        { icon: "icon-kecheng-weixuanzhong", colorName: "nav_1", name: "课程", path: "/subject/list" },
        { icon: "icon-yulan", colorName: "nav_2", name: "题库", path: "/practise-library" },
        { icon: "icon-cloud-folder", colorName: "nav_3", name: "云盘", path: "/cloud-disk" }
      ],

      popularList: [],
      invitedShow: false
    };
  },
  created () {
    window.acceptQRcode = code => {
      const codeJson = this.isJsonString(code);
      // this.joinClass(codeJson && codeJson.CIID ? codeJson.CIID : codeJson, "scan");
      console.log("acceptQRcode:", codeJson);
      return true;
    };
  },
  mounted () {
    this.getCourseData();
  },
  methods: {
    jump (index) {
      if (this.navList[index].path) {
        this.$router.push(this.navList[index].path);
        return;
      }
      const jump = document.getElementsByClassName("sub_nav");
      // 获取需要滚动的距离
      const total = jump[index - 1].offsetTop;
      const currentY =
        document.documentElement.scrollTop || document.body.scrollTop;
      this.scrollAnimation(currentY, total);
    },
    scrollAnimation (currentY, targetY) {
      // 计算需要移动的距离
      const needScrollTop = targetY - currentY;
      let _currentY = currentY;
      setTimeout(() => {
        // 一次调用滑动帧数，每次调用会不一样
        const dist = Math.ceil(needScrollTop / 10);
        _currentY += dist;
        window.scrollTo(_currentY, currentY);
        // 如果移动幅度小于十个像素，直接移动，否则递归调用，实现动画效果
        if (needScrollTop > 10 || needScrollTop < -10) {
          this.scrollAnimation(_currentY, targetY);
        } else {
          window.scrollTo(_currentY, targetY);
        }
      }, 1);
    },
    // 开启扫一扫
    scanOpen () {
      this.$refs.DropDown.hide();
      sendToAPP(FuncType.openQRcode, "");
    },
    // 打开输入邀请码
    codeOpen () {
      this.$refs.DropDown.hide();
      this.invitedShow = true;
      setTimeout(() => {
        this.$refs["digit"].init();
      }, 0);
    },
    submitInvitedCode (arr) {
      console.log("arr:", arr);
    },

    /**
     * 获取最新的课程
     */
    getCourseData () {
      this.popularList = [];
      this.$mainHttp.course.dataList({
        userID: this.$store.getters.userID
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });
          res.Datas.forEach(item => {
            this.popularList.push({
              imgSrc: defaultImg,
              title: item["CourseName"],
              createTime: formatDate(item["CreateTime"], "yyyy-MM-dd"),
              remark: formatDate(item["CreateTime"], "yyyy-MM-dd"),
              id: item["CourseID"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
      }).catch(() => {
      });
    },
    popularClick (item) {
      this.$store.commit("SET_SUBJECT", item);
      this.$store.commit("SET_SUBJECT_ID", item.id);
      this.$router.push("/subject/view");
    }
  }
};
</script>

